import React, { useState } from 'react'
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css'


export default function Contact() {
    const [title, setTitle] = useState('');
    const [email, setEmail] = useState('');
    const [body, setBody] = useState();

    const submit = () => {
        fetch('/list-mail', {
            method: 'POST',
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ title, body, email })
        }).then(res => {
            console.log(res);
        }).catch(err => console.log(err))
    }
    const modules = {
        toolbar: [['bold', 'italic', 'underline', 'strike'], [{ 'header': 1 }, { 'header': 2 }], [{ 'list': 'ordered' }, { 'list': 'bullet' }], [{ 'indent': '-1' }, { 'indent': '+1' }], [{ 'direction': 'rtl' }], [{ 'size': ['small', false, 'large', 'huge'] }], [{ 'font': [] }], [{ 'color': [] }, { 'background': [] }]]
    }
    return (
        <div style={{ maxWidth: '800px', padding: '25px' }}>
            <h3 className='title m-4'>Title</h3>
            <input type="text" value={title} style={{ width: '100%' }} onChange={e => setTitle(e.target.value)} />
            <h3 className='title m-4'>E-mail</h3>
            <input type="text" value={email} style={{ width: '100%' }} onChange={e => setEmail(e.target.value)} />
            <h3 className='title m-4'>Body</h3>
            <ReactQuill modules={modules} value={body || ''} onChange={setBody} />
            <div style={{ marginTop: '2em' }}>
                <button onClick={submit} className="butn butn-hot">SEND</button>

            </div>
        </div>
    )
}
