import React, { useState, useEffect } from "react";
import { validate } from "./item-validate";
import { useHistory } from "react-router-dom";
import ImageUploader from "./imageUploader";

export default function Additem(props) {
  const [product_id, setId] = useState(null);
  const [files, setFiles] = useState([]);
  const [form, setForm] = useState({
    name: "",
    title: "",
    price: 0,
    amount: 0,
    weight: 0,
    disc: "",
    subDisc: "",
    dir: "",
    ing: "",
  });
  const [price, setPrice] = useState({
    d: "",
    c: "",
  });

  const [errors, setErrors] = useState({
    name: null,
    title: null,
    price: null,
    amount: null,
    image: null,
    success: null,
  });
  const handleInput = (name, value) => {
    setForm(old => ({ ...old, [name]: value }))
  };
  //handles price differently to other form fields due to no maxLength of
  //numbers and issues with setting floats in state
  const handlePrice = (value, unit) => {
    const regex = RegExp(/^(\s*|\d+)$/);
    if (regex.test(value)) {
      setPrice(old => ({ ...old, [unit]: value }));
    }
  };
  let history = useHistory();
  //onChange of dollars or cents creates new float amount in submition form
  useEffect(() => {
    const $ = price.d + "." + price.c;
    setForm(old => ({ ...old, price: parseFloat($) }));
  }, [price]);
  //checks for link based props for update vs. new item based on if product_id
  useEffect(() => {
    let controller = new AbortController();
    if (props.location.state) {
      const { product_id } = props.location.state;
      setId(product_id);
      fetch("/get-item", {
        signal: controller.signal,
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ product_id }),
      })
        .then((res) => res.json())
        .then((item) => {
          setForm(item);
          const priceArray = item.price.toString().split(".");
          const price = { d: priceArray[0], c: priceArray[1] };
          setPrice(price);
        })
        .catch((err) => console.log(err));
    } else {
      setId(null);
    }
    return () => {
      controller.abort();
    };
  }, [props.location]);
  //on dismount, deletes images if not saved

  //submits form to save item in db for display
  const handleSubmit = () => {
    let formErrors = validate(form);
    let pass = true;
    for (var msg in formErrors) {
      if (formErrors[msg]) {
        pass = false;
      }
    }
    if (pass) {
      fetch(product_id ? "/upd-item" : "/add-item", {
        method: "POST",
        credentials: "same-origin",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ form, product_id }),
      })
        .then((res) => {
          if (res.status === 200) {
            res.json().then((data) => {
              setId(data[0]);
              let imgUpdate = [];
              files.forEach((file) => {
                imgUpdate.push({
                  id: file.id,
                  update: {
                    product_id: data[0],
                    sort: file.sort,
                  },
                });
              });
              fetch("/upd-image", {
                method: "POST",
                credentials: "same-origin",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(imgUpdate),
              }).then((res) => {
                if (res.status === 200) {
                  history.goBack();
                }
                if (res.status === 400) {
                  new Error("Error updating images");
                }
              });
            });
          }
          if (res.status === 400) {
            res.json().then((data) => {
              setErrors(data);
            });
          }
        })
        .catch();
    } else {
      setErrors(formErrors);
    }
  };

  return (
    <div className="tStyle">

      <div className="form-group col-6">
        <h5 className="title m-2">Name<span className="errorMessage">*</span> </h5>
        <input
          type="text"
          value={form.name}
          onChange={(e) => handleInput("name", e.target.value)}
        />
        {errors.name ? (
          <small className="errorMessage">{errors.name}</small>
        ) : null}
      </div>


      <div className="form-group col-6">
        <h5 className="title m-2">Title<span className="errorMessage">*</span></h5>
        <input
          type="text"
          value={form.title}
          onChange={(e) => handleInput("title", e.target.value)}
        />
        {errors.title ? (
          <small className="errorMessage">{errors.title}</small>
        ) : null}
      </div>
      <div className="">
        <div className="form-group col-6">
          <h5 className="title m-2">Price<span className="errorMessage">*</span></h5>
          <div className="d-flex">
            <h5 className="title m-2"> $ </h5>
            <input
              type="text"
              placeholder="0"
              value={price.d}
              onChange={(e) => handlePrice(e.target.value, "d")}
            />
            <h5 className="title m-2"> . </h5>
            <input
              type="text"
              maxLength="2"
              placeholder="00"
              value={price.c}
              onChange={(e) => handlePrice(e.target.value, "c")}
            />
          </div>
          {errors.price ? (
            <small className="errorMessage">{errors.price}</small>
          ) : null}
        </div>
        <div className="form-group col-6">
          <h5 className="title m-2">Stock amount<span className="errorMessage">*</span></h5>
          <input
            type="number"

            value={form.amount}
            onChange={(e) => handleInput("amount", parseInt(e.target.value))}
            onClick={(e) => e.target.select()}
          />
          {errors.amount ? (
            <small className="errorMessage">{errors.amount}</small>
          ) : null}
        </div>
        <div className="col-6">
          <h5 className="title m-2">Weight in grams<span className="errorMessage">*</span></h5>
          <input
            type="number"

            value={form.weight}
            onChange={(e) => handleInput("weight", parseInt(e.target.value))}
            onClick={(e) => e.target.select()}
          />
          {errors.title ? (
            <small className="errorMessage">{errors.weight}</small>
          ) : null}
        </div>
      </div>

      <div>
        <h5 className="title m-2">Description</h5>
        <textarea

          value={form.disc}
          onChange={(e) => handleInput("disc", e.target.value)}
        />
      </div>

      <div>
        <h5 className="title m-2">Suitable for</h5>
        <textarea

          value={form.subDisc}
          onChange={(e) => handleInput("subDisc", e.target.value)}
        />
      </div>

      <div>
        <h5 className="title m-2">Directions</h5>
        <textarea

          value={form.dir}
          onChange={(e) => handleInput("dir", e.target.value)}
        />
      </div>

      <div>
        <h5 className="title m-2">Ingredients</h5>
        <textarea

          value={form.ing}
          onChange={(e) => handleInput("ing", e.target.value)}
        />
      </div>
      <ImageUploader returnFiles={setFiles} componentID={product_id} />
      <div>
        {
          (() => {
            let err = false;
            let str = "Validation failed on ( "
            for (const key in errors) {
              if (errors[key]) {
                str += `${key}, `;
                err = true;
              }
            }
            str = str.slice(0, -2)
            str += ' )';
            return (err ? <small className="errorMessage">{str}</small>
              :
              null)
          })()
        }
        <div style={{ display: '-webkit-inline-flex' }}><button className="butn butn-hot" onClick={handleSubmit}>
          ADD ITEM
        </button>
          <button className="butn butn-not ml-4" onClick={() => history.goBack()}>CANCEL</button></div>
      </div>
    </div>
  );
}
