import React, { useState } from "react";
import { Modal } from "react-bootstrap";
import Carousel from "fade-carousel";

export default function ItemMod({ item, onAdd, added }) {

  const [amount, setAmount] = useState(1);
  const [info, setinfo] = useState("dets");

  const add = () => {
    onAdd(item.product_id, amount);
    added();
  };

  const title = {
    textAlign: "center",
  };
  const list = {
    listStyleType: "none",
    lineHeight: ".5",
  };
  let menu;

  if (info === "dets") {
    menu = (
      <div>
        <p className="100 text-center">{item.disc}</p>
        <p className="100 text-center">{item.subDisc}</p>
      </div>
    );
  }
  if (info === "use") {
    menu = (
      <div>
        <p className="100 text-center">{item.dir}</p>
      </div>
    );
  }
  if (info === "ing") {
    menu = (
      <div>
        <p className="100 text-center">{item.hero}</p>
        <p className="100 text-center">{item.ing}</p>
      </div>
    );
  }

  return (
    <div>
      <Modal.Body>
        <Carousel
          divStyle={{
            width: "100%",
            height: "500px",
            borderRadius: "10px",
            border: '1px solid #c7c7c7'
          }}
        >
          {item.images.map((i, index) => (
            <img
              key={index}
              src={i.ref}
              style={{
                height: "100%",
                width: "auto",
                margin: "auto",
              }}
              alt=""
            />
          ))}
        </Carousel>

        <div>
          <h5 style={title}>{item.title}</h5>
        </div>
        <div className="pt-4">
          <ul style={list} className="d-flex justify-content-around">
            <li
              className={info === "dets" ? 'secondF' : 'primaryF'}
              onClick={() => setinfo("dets")}
            >
              <h6>DETIALS</h6>
            </li>
            <li
              className={info === "dir" ? 'secondF' : 'primaryF'}
              onClick={() => setinfo("dir")}
            >
              <h6>DIRECTIONS</h6>
            </li>
            <li
              className={info === "ing" ? 'secondF' : 'primaryF'}
              onClick={() => setinfo("ing")}
            >
              <h6>INGREDIENTS</h6>
            </li>
          </ul>
          <hr />
        </div>
        {menu}
      </Modal.Body>
      <Modal.Footer>
        {item.amount > 0 ?
          (<div className="d-flex justify-content-between">
            <div className="d-flex pr-4">
              <h4 className="pr-3 secondF">
                AMOUNT{" "}
              </h4>
              <select onChange={(e) => setAmount(parseInt(e.target.value))}>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
                <option value="6">6</option>
                <option value="7">7</option>
                <option value="8">8</option>
                <option value="9">9</option>
                <option value="10">10</option>
                <option value="11">11</option>
                <option value="12">12</option>
              </select>
            </div>
            <div>
              <button onClick={add} className="butn butn-hot">
                ADD
              </button>
            </div>
          </div>)
          :
          (<div className="w-100 text-center">
            <h3 className="secondF">SOLD OUT</h3>
          </div>)
        }
      </Modal.Footer>
    </div>
  );
}
