import React, { useEffect, useState } from 'react';
import MenuItem from './components/menuItem';
import Modal from '../widgets/modal';
import ItemMod from './components/itemMod'

export default function Menu({ sendItems }) {
  const [fetcher, setFetcher] = useState(false)
  const [items, setItems] = useState([])
  const [id, setId] = useState(0);
  const [show1, setShow1] = useState(false);
  const [show2, setShow2] = useState(false);
  const [added, setAdded] = useState(false)

  useEffect(() => {
    const abortController = new AbortController();
    fetch('/get-stock')
      .then(res => res.json())
      .then(data => {
        setItems(data);
      })
    return abortController.abort()
  }, [fetcher]);

  useEffect(() => {
    sendItems(items)
  }, [items, sendItems])

  const shop = {
    display: 'flex',
    flexFlow: 'row wrap',
    justifyContent: 'center'
  }

  const onAdd = (id, amount = 1) => {
    setShow1(false);
    setShow2(true);
    setAdded(true);
    const old = [...items]
    const idx = old.findIndex(i => i.product_id === id)
    old[idx].value += amount;
    setItems(old)
  }
  const showItem = (id) => {
    setId(id);
    setShow1(true);
    setAdded(false);
  }
  const item = items.find(i => i.product_id === id)

  return (
    <div>
      <div>
        <button className="butn butn-hot" onClick={() => setFetcher(old => !old)}>REFRESH</button>
      </div>
      <div style={shop}>
        {items.map(i => (
          <MenuItem
            key={i.product_id}
            i={i}
            onAdd={onAdd}
            showItem={() => showItem(i.product_id)}
          />
        ))}
      </div>
      {item ? <Modal show={show1} hide={() => setShow1(false)} title={item.name}>
        <ItemMod item={item} onAdd={onAdd} added={() => setAdded(true)} />
      </Modal> : null}
      {added ? <Modal show={show2} hide={() => setShow2(false)}>
        <h5>ITEM ADDED</h5>
      </Modal> : null}
    </div>
  );
}


