import React, { useState, useEffect, useRef, useCallback } from "react";
import { IoIosChatbubbles } from "react-icons/io";
import { MdClose } from "react-icons/md";
import { socket } from "../socket";
import { Spring } from "react-spring/renderprops";

export default function Chat() {
  //meat n potatoes of the message, setting the open and closed state...
  const [open, setOpen] = useState(false);
  //hover event for icon
  const [hover, setHover] = useState(false);
  // setting the convo object's array
  const [conv, setConv] = useState([]);
  //setting the current message
  const [message, setMessage] = useState("");
  //recieving messages 'FromAPI' and updating convo with recieved message
  useState(() => {
    socket.on("FromAPI", (res) => {
      const data = JSON.parse(res);
      setConv((old) => [...old, data]);
    });
  }, []);
  //sending messages 'ToAPI' using the current message then adding to convo array
  const send = (e) => {
    e.preventDefault();
    if (message.length === 0) return;
    socket.emit("ToAPI", message);
    const mess = {
      text: message,
      sender: {
        name: "You",
        uid: "to",
      },
    };
    setConv((old) => [...old, mess]);
    //and resetting message for new messages
    setMessage("");
  };
  //scrolling to bottom automaticlly
  const messageRef = useRef(null);

  const scrollToBottom = useCallback(() => {
    if (open) messageRef.current.scrollIntoView({ behavior: "smooth" });
  }, [open])

  useEffect(() => {
    scrollToBottom();
  }, [conv, scrollToBottom]);

  //initial position on page
  const box = {
    position: "fixed",
    bottom: "10px",
    right: "10px",
    zIndex: "2000",
  };
  const closed = {
    position: "absolute",
    bottom: "35px",
    right: "0",
  };
  //opened chat css
  const opened = {
    backgroundColor: "#373a3d",
    width: "300px",
    height: "400px",
    borderRadius: "5px",
    border: '1px solid #c7c7c7',
    display: "flex",
    flexDirection: "column",
  };
  const header = {
    borderRadius: "5px 5px 0 0",
    padding: "10px",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    color: "white",
  };
  const messageBox = {
    flexGrow: "1",
    backgroundColor: "#656c70",
    overflowY: "scroll",
    display: "flex",
    flexDirection: "column",
  };
  const input = {
    padding: "5px",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  };

  return (
    <div style={box}>
      {open ? (
        <Spring
          from={{
            opacity: 0,
            marginBottom: -500,
          }}
          to={{
            opacity: 1,
            marginBottom: 0,
          }}
        >
          {(props) => (
            <div style={props}>
              <div style={opened}>
                <div style={header}>
                  <h6 style={{ margin: "0" }}>CHAT</h6>
                  <MdClose
                    color="#c7c7c7"
                    onClick={() => setOpen(false)}
                  />
                </div>
                <div style={messageBox}>
                  {conv.map((i, index) => (
                    <div key={index} className={i.sender.uid + " message-box"}>
                      <small>{i.sender.name}</small>
                      <p style={{ marginBottom: "0" }}>{i.text}</p>
                    </div>
                  ))}
                  <div ref={messageRef} />
                </div>
                <form onSubmit={send} style={input}>
                  <input
                    type="text"
                    name=""
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                  />
                  <button type="submit" className="butn butn-hot">
                    SEND
                  </button>
                </form>
              </div>
            </div>
          )}
        </Spring>
      ) : (
          <div style={closed}>
            <IoIosChatbubbles
              size={hover ? "3.2em" : "3em"}
              color="#9effab"
              onClick={() => setOpen(true)}
              onMouseEnter={() => setHover(true)}
              onMouseLeave={() => setHover(false)}
            />
          </div>
        )}
    </div>
  );
}
