import React, { useState, useEffect } from 'react'
import { IoIosArrowDown, IoIosArrowUp } from 'react-icons/io'
import { Link } from 'react-router-dom';
import { Modal } from 'react-bootstrap';

export default function ItemList() {
    const [products, setProducts] = useState([])
    const [deleted, setDeleted] = useState([]);
    const [show, setShow] = useState(false);

    useEffect(() => {
        const abortController = new AbortController();
        fetch('/get-stock')
            .then(res => res.json())
            .then(data => setProducts(data))
        return abortController.abort()
    }, [])

    const save = () => {
        const abortController = new AbortController();
        fetch('/products-update', {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ products, deleted })
        })
            .then(res => res.json())
            .then(data => { console.log(data); setProducts(data) })
            .catch(err => console.log(err))
        setShow(false);
        return abortController.abort()
    }

    const itemDelete = (index, id) => {
        let copyProducts = [...products];
        let copyDeleted = [...deleted];
        copyProducts.splice(index, 1);
        copyDeleted.push(id)
        updateMove(copyProducts);
        setDeleted(copyDeleted);
    }

    const moveUp = (index) => {
        let copy = [...products];
        if (index > 0) {
            copy.splice(index - 1, 0, copy.splice(index, 1)[0]);
        }
        updateMove(copy)
    }
    const moveDown = (index) => {
        let copy = [...products];
        if (index <= copy.length) {
            copy.splice(index + 1, 0, copy.splice(index, 1)[0]);
        }
        updateMove(copy)
    }
    const updateMove = (array) => {
        array.forEach((i, index) => {
            i.sort = index
        })
        setProducts(array);
    }


    const move = { display: 'flex', flexDirection: 'column' }
    const mini = { height: '80px', width: '80px', overflow: 'hidden' }
    const photo = { height: '100%', width: 'auto' }

    return (
        <div>
            <div className="table-responsive">
                <Modal show={show} size="sm">
                    <Modal.Body>
                        <h5 className='title'>Are you sure?</h5>
                        <p>Any changed made now will be permanent.</p>
                        <button className='butn butn-hot' onClick={save}>SAVE</button>
                        <button className='butn butn-not' onClick={() => setShow(false)}>CANCEL</button>
                    </Modal.Body>
                </Modal>
                <table className="table">
                    <thead>
                        <tr>
                            <th></th>
                            <th><Link to="/examples/add-item"><button className='butn butn-not'>ADD ITEM</button></Link></th>
                            <th></th>
                            <th></th>

                            <th><button className='butn butn-not' onClick={() => { setShow(true) }}>SAVE</button></th>
                        </tr>
                    </thead>

                    <tbody>
                        {products.map((i, index) => (
                            <tr key={i.product_id}>
                                <td style={{ verticalAlign: 'middle' }}>
                                    <div style={move}>
                                        <IoIosArrowUp onClick={() => moveUp(index)} />
                                        <IoIosArrowDown onClick={() => moveDown(index)} />
                                    </div>
                                </td>
                                <td style={{ verticalAlign: 'middle' }}>
                                    <div style={mini}>
                                        {i.images.length > 0 ?
                                            <img src={i.images[0].ref + "/tr:h-100"} alt="" style={photo} />
                                            :
                                            null
                                        }
                                    </div>
                                </td>
                                <td style={{ verticalAlign: 'middle' }}>{i.name}</td>
                                <td style={{ verticalAlign: 'middle' }}>
                                    <Link to={{
                                        pathname: "/examples/add-item",
                                        state: { product_id: i.product_id }
                                    }}>
                                        <button className="butn butn-hot">EDIT</button>
                                    </Link>
                                </td>
                                <td style={{ verticalAlign: 'middle' }}>
                                    <button className="butn butn-not" onClick={() => itemDelete(index, i.product_id)} >DELETE</button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    )
}
