import React from 'react'
import { Link } from 'react-router-dom';

export default function Header() {
    const pad = { padding: '10px' }
    return (
        <div style={{ display: "flex", justifyContent: "space-between", alignItems: 'center' }}>
            <div className="logo" style={{ textAlign: 'center', padding: "20px 0 0 20px" }}>
                <Link to="/">
                    <p>psyKressX</p>
                </Link>
            </div>
            <div className="links" style={{ display: "flex", padding: "25px" }}>
                <div style={pad}>
                    <Link to="/resume">
                        <h2 className="headlinks" >RESUME</h2>
                    </Link>
                </div>
                <div style={pad}>
                    <Link to="/examples">
                        <h2 className="headlinks" >EXAMPLES</h2>
                    </Link>
                </div>
                <div style={pad}>
                    <Link to="/contact">
                        <h2 className="headlinks" >CONTACT ME</h2>
                    </Link>
                </div>
            </div>
        </div>
    )
}
