import React from 'react'
import { Modal } from 'react-bootstrap';
import { MdClose } from 'react-icons/md'
import 'bootstrap/dist/css/bootstrap.min.css';

export default function MyModal({ children, show, hide, title }) {
    return (
        <Modal show={show} onHide={hide} animation={true} centered dialogClassName="my-modal" size="lg" >
            <Modal.Header style={{ display: 'flex', backgroundColor: "#373a3d", borderBottom: "none", textAlign: "center" }}>
                <h3 style={{ color: "white" }}>{title}</h3>
                <MdClose style={{ justifySelf: "flex-end" }} onClick={hide} color="#c7c7c7" />
            </Modal.Header>
            <Modal.Body style={{ maxHeight: '900px', overflowY: 'auto', backgroundColor: "#656c70", color: "white" }}>
                {children}
            </Modal.Body>
        </Modal>
    )
}
