import React from 'react'

import CopySpan from '../widgets/copySpan'


export default function ViewOrder({ items }) {

    const order = () => {
        if (items.length < 1) return;
        fetch(`/processOrder`, {
            method: "POST",
            credentials: "same-origin",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(items)
        })
            .then(res => res.text())
            .then(text => window.location.assign(text))
    }

    const hasItems = (
        <div>
            <div className="table-responsive">
                <h5>View Order</h5>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Amount</th>
                            <th>Price</th>
                            <th>Total</th>
                        </tr>
                    </thead>
                    <tbody>
                        {items.map((i, index) => (
                            <tr key={index}>
                                <td>{i.name}</td>
                                <td>{i.value}</td>
                                <td>{i.price}</td>
                                <td>{(i.price * i.value).toFixed(2)}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
            <h3 className="errorMessage">!IMPORTANT! Sandbox mode, copy and paste BOTH credentials below somewhere before proceeding</h3>
            <ul>
                <li>
                    Sandbox User :  <CopySpan text="sb-4xdab1459551@personal.example.com" />
                </li>
                <li>
                    Password: <CopySpan text="wc@1rvAz" />
                </li>
            </ul>
            <button className="butn butn-hot" onClick={order}>CHECK OUT</button>
        </div>
    )

    const noItems = (<div>
        <h5>Please add items to cart from user store front to continue</h5>
    </div>)
    return (items.length > 0 ? hasItems : noItems)
}
