import React, { useState } from 'react';
import Items from '../examples/items';
import Menu from '../examples/menu';
import ViewOrder from '../examples/viewOrder';
import CompBox from '../widgets/compBox';

export default function Examples() {
    const [items, getItems] = useState([])
    return (
        <div style={{ overflow: "auto", maxWidth: '1300px' }}>
            <div style={{ padding: "20px" }}>
                <p>Below are examples copied over from my E-commerce platform, built using react hooks, node-express, pSQL, websockets, 3rd party APIs</p>
                <p>All images have dynamically resolutions using imageKit url queries</p>
                <p>Components built include:</p>
            </div>
            <ul>
                <li>
                    <CompBox>
                        <h3>ChatBox</h3>
                        <p>Chat with me any time live with the chat box fixed to the bottom right.</p>
                    </CompBox>

                </li>
                <li>
                    <CompBox>
                        <h3>Admin item maker</h3>
                        <p>Create, rearrange, edit or delete items from within the section below. ADD or EDIT will take you to the item form that allows anyone to create their own shop with ease. Rearrange with the arrows or delete items, then, hit save to alter the item list and its order.</p>
                        <Items />
                    </CompBox>
                </li>
                <li>
                    <CompBox>
                        <h3>User store front</h3>
                        <p>Use the client facing store for purchasing and item viewing. Click the item to bring up a stylish modal with product info and images, displayed on my custom carousel.</p>
                        <p>Visit my published package page for fade-carousel <a href="https://www.npmjs.com/package/fade-carousel" target="blank">HERE</a> on npm.</p>
                        <Menu sendItems={getItems} />
                    </CompBox>
                </li>
                <li>
                    <CompBox>
                        <h3>Paypal API integration</h3>
                        <p>Create an order by adding items from the store front above, then, finalise purchase through Paypal Sandbox checkout with credentials in green.</p>
                        <ViewOrder items={items.filter(i => i.value > 0)} />
                    </CompBox>

                </li>
            </ul>
        </div>
    )
}
