import React, { useState } from 'react';

export default function MenuItem({ i, onAdd, showItem }) {
  const [fade, setFade] = useState(false);

  const image = {
    height: '100%',
    margin: 'auto'
  };

  const imgDiv = {
    height: "290px",
    width: "290px",
    margin: "50px auto 20px auto",
    overflow: 'hidden'
  };
  const iHeader = {
    textAlign: 'center',
    padding: "10px",
    bottom: '0',
    width: '100%',
    lineHeight: '.5'
  };
  const hover = {
    height: "290px",
    width: "290px",
    position: 'absolute',
    zIndex: '10'
  };
  const sold = {
    padding: '2px 5px 3px 5px',
    border: 'none',
    height: '30px',
    width: "140px",
    textAlign: 'center',
    backgroundColor: 'rgb(117, 117, 117)'
  }
  const shadow = {
    pointerEvents: "none"
  }
  return (
    <div className='box'>
      <div
        className="centered"
        style={imgDiv}
        onMouseEnter={() => setFade(true)}
        onMouseLeave={() => setFade(false)}
      >
        {fade ? <div className='centered' style={hover} onClick={showItem}>
          {i.amount > 0 ?
            <button onClick={e => { onAdd(i.product_id); e.stopPropagation() }} className="butn butn-hot">ADD</button>
            :
            <div style={sold}>
              <p style={{ color: 'white' }}>SOLD OUT</p>
            </div>
          }
        </div>
          : null}
        <img className={fade ? "fader" : ""} style={image} src={i.images.length > 0 ? i.images[0].ref + "/tr:h-300" : process.env.PUBLIC_URL + "no-image-icon-23494.png"} alt="" />
      </div>

      <div style={shadow}>
        <div style={iHeader}>
          <h3 style={{ fontSize: '1.5em' }} className="title">{i.name}</h3>
          <p>{i.title}</p>
          {i.amount > 0 ?
            <p style={{ fontSize: '1em', color: '#757575' }}><b>${i.price}</b></p>
            :
            <p style={{ fontSize: '1em', color: '#757575' }}><b>SOLD OUT</b></p>
          }
        </div>
      </div>
    </div >
  );
}