import React, { useState, useEffect } from 'react'

export default function Success() {
    const [orders, setOrders] = useState([]);

    useEffect(() => {
        const abort = new AbortController()
        const url = window.location.href;
        const query = url.split("?")
        fetch('/success?' + query[1])
            .then(() =>
                fetch("/get-orders")
                    .then(res => res.json())
                    .then(data => setOrders(data))
            ).catch(err => console.log(err))
        return abort.abort();
    }, [])

    useEffect(() => {



    }, [])

    return (
        <div className="tStyle">
            <h3>SUCCESS!!</h3>
            <p>check the bottom of the table for your newly created order</p>
            <table>
                <thead>
                    <tr>
                        <th>ORDER ID</th>
                        <th>PRICE</th>
                        <th>ORDER DATE</th>
                    </tr>
                </thead>
                <tbody>
                    {orders.map((o, index) =>
                        <tr key={index}>
                            <td>{o.order_id}</td>
                            <td>{o.price}</td>
                            <td>{o.order_date}</td>
                        </tr>
                    )}
                </tbody>
            </table>
        </div>
    )
}
