import React from 'react'
import { FaNode, FaReact } from 'react-icons/fa'

export default function FloatFooter() {

    return (
        <div className="foothide" style={{ position: "fixed", bottom: '0px', left: '0px', padding: '20px', textAlign: 'center' }}>
            <h5>POWERED BY</h5>
            <div style={{ display: 'flex' }}>
                <div style={{ padding: "0 20px" }}>
                    <FaReact size="3em" />
                </div>
                <div style={{ padding: "0 20px" }}>
                    <FaNode size="4em" />
                </div>
            </div>
        </div>
    )
}
